.burger-ingredient {
  border-radius: 20px;
  position: relative;
}

.burger-ingredient_interactive {
  cursor: pointer;
}

.burger-ingredient_is-picked {
  background-color: var(--colors-interface-accent);
}

.burger-ingredient__image-wrapper {
  display: flex;
  justify-content: center;
  pointer-events: none;
}

.burger-ingredient__price-wrapper {
  justify-content: center;
}

.burger-ingredient__title {
  text-align: center;
}
