.container {
  display: flex;
  flex-direction: row;
  gap: 60px;
  margin: 0 auto;
  width: 1280px;
}

.links {
  display: flex;
  flex-direction: column;
  max-width: 320px;
  align-items: flex-start;
  text-align: start;
  text-decoration: none;
}

.link {
  height: 64px;
  text-decoration: none;
}

.active {
  color: #F2F2F3
}

.forms {
  display: flex;
  flex-direction: column;
}

.buttons {
  display: flex;
  justify-content: flex-end;
}
